/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
*, *:before, *:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html, body, #root,
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html,
body,
#root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  min-height: 100%;
}

body {
  font-size: 10px;
  font-family: 'Open Sans', sans-serif;
  line-height: 1.25;
}

#root {
  background: -webkit-gradient(linear, left top, left bottom, from(#9c27b0), to(#fff));
  background: -webkit-linear-gradient(top, #9c27b0 0%, #fff 100%);
  background: -o-linear-gradient(top, #9c27b0 0%, #fff 100%);
  background: linear-gradient(to bottom, #9c27b0 0%, #fff 100%);
}

::-moz-selection {
  background-color: #9c27b0;
  color: #fff;
}

::selection {
  background-color: #9c27b0;
  color: #fff;
}


